import React from "react";
import './Home.css'
import mypic from './mypic.JPG'
import cv from './Narh Samuel_CV.pdf'

const Home = () =>{
    return(
        <section>
        <div className="home">
             <div className="pt6 pa4 tc b home_item">
                <p style={{color:'green'}}>Hello Friend !!!</p>
                <p>I'm a fullstack Web Developer</p>
                <a href={`https://mailchi.mp/603fd63a20ae/contactme`}> 
                    <button className="center b button">Hire Me
                     </button>
                </a>
                <a href={cv} download> 
                    <button className="center b button">Download CV
                     </button>
                </a>
            </div>
            <div>
                <img src={mypic}  alt="my_profile_pic" id="mypic" />
            </div>
        </div>
        </section>
    )
}

export default Home;